/** LSX Starter Plugin styles (admin). @package lsx-business-directory */
/** The CMB2 Settings Page Tabbed Theme. @package lsx-business-directory */
/* CMB2 Settings Styles */
.wp-admin.business-directory_page_lsx-business-directory-settings .wp-filter { background: none; border: none; box-shadow: none; margin: 0; padding-left: 0; }

.wp-admin.business-directory_page_lsx-business-directory-settings .wp-filter .filter-links .current { border-bottom: none; font-weight: 600; color: #23282d; }

.wp-admin.business-directory_page_lsx-business-directory-settings .wp-filter .filter-links > li > a { border-bottom: none; margin: 2px; }

.wp-admin.business-directory_page_lsx-business-directory-settings .wp-filter .filter-links > li:nth-of-type(1) > a { margin-left: 0; }

.wp-admin.business-directory_page_lsx-business-directory-settings .cmb2-wrap.form-table { margin-top: 0; border: 1px solid transparent; }

.wp-admin.business-directory_page_lsx-business-directory-settings .cmb2-wrap.form-table .tab.tab-nav .cmb-row { display: flex; flex-direction: row; }

.wp-admin.business-directory_page_lsx-business-directory-settings .cmb2-wrap.form-table .tab.tab-nav .cmb-row.cmb-type-title { display: none; }

.wp-admin.business-directory_page_lsx-business-directory-settings .cmb2-wrap.form-table .tab.tab-nav .cmb-row div.cmb-th { text-align: left; padding: 20px 10px 20px 0; width: 200px; font-size: 14px; line-height: 1.3; font-weight: 600; color: #23282d; text-shadow: none; vertical-align: baseline; }

.wp-admin.business-directory_page_lsx-business-directory-settings .cmb2-wrap.form-table .tab.tab-nav .cmb-row div.cmb-td { text-align: left !important; padding: 20px 10px 20px 0 !important; margin-right: 15px; }

.wp-admin.business-directory_page_lsx-business-directory-settings .cmb2-wrap.form-table .tab.tab-nav .cmb-row div.cmb-td select { font-size: 14px; line-height: 2; color: #32373c; border-color: #7e8993; box-shadow: none; border-radius: 3px; padding: 0 24px 0 8px; min-height: 30px; max-width: 25rem; -webkit-appearance: none; background-size: 16px 16px; cursor: pointer; vertical-align: middle; }

.wp-admin.business-directory_page_lsx-business-directory-settings .cmb2-wrap.form-table .tab.tab-nav .cmb-row div.cmb-td .cmb2-media-status { padding-top: 10px; }

.wp-admin.business-directory_page_lsx-business-directory-settings .cmb2-wrap.form-table .tab.tab-nav .cmb-row div.cmb-td input.cmb2-upload-file { margin-right: 15px; }

.wp-admin.business-directory_page_lsx-business-directory-settings .cmb2-wrap.form-table .tab.tab-nav .cmb-row p.cmb2-metabox-description { margin: 2px 0 5px; color: #666; font-style: italic; line-height: 1.5; margin-top: 4px; margin-bottom: 0; }

.wp-admin.business-directory_page_lsx-business-directory-settings .cmb2-wrap.form-table .tab.tab-nav .cmb-row input[type=radio] { margin-top: -4px; margin-right: 4px; float: none; }

/** The CMB2 Settings Page Tabbed Theme. @package lsx-business-directory */
